import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home,
        children: [{ //首页
            path: '/',
            name: 'index',
            component: () =>
                import ('@/components/front/index')
        }, { //首页
            path: '/index',
            name: 'index',
            component: () =>
                import ('@/components/front/index')
        }, { //生活
            path: '/life',
            name: 'life',
            component: () =>
                import ('@/components/front/life')
        }, { //相册
            path: '/photo',
            name: 'photo',
            component: () =>
                import ('@/components/front/photo')
        }, { //it技术分享
            path: '/it',
            name: 'it',
            component: () =>
                import ('@/components/front/it')
        }, { //关于我
            path: '/my',
            name: 'my',
            component: () =>
                import ('@/components/front/my')
        }, { //搜索
            path: '/search',
            name: 'search',
            component: () =>
                import ('@/components/front/search')
        }]
    }, {
        path: '/Login',
        name: 'Login',
        // 页面在进入此组件时才加载此组件
        component: () =>
            import ('@/components/Login')
    }, {
        path: '/Template',
        name: 'Template',
        // 页面在进入此组件时才加载此组件
        component: () =>
            import ('@/components/Template')
    }]
})


